import React from 'react';

export default class WordImg extends React.Component{
    constructor(){
        super()
        this.state={
            newsData:[{name:1,root:2}],
            content:[
                {item:'内容一'},
                {item:'内容二'},
                {item:'内容三'}
            ],
        }

    }
    componentDidMount(){
        var newdateArr = "badiu"

        var _this = this;
        $.ajax({
            url:"/api/user/signup",

            type:"get",
            async:'false'
        }).done(function(data){

            console.log( $.type(data))

            _this.setState({newsData:data})
        })
    }
    render(){



        return(
            <div className="newsImg">
                {this.state.newsData.map(function (item,index) {
                    return(
                        <div>
                            <img src={'public/css/img'+item.img}/>
                            <li className="fx1" key={index}>{item.word}</li>

                        </div>
                        )

                })}
            </div>
        )
    }
}


